import { h, Component } from 'preact';
import style from './style.css';
import Button from '../../components/actionButton/';
import { Link } from 'preact-router/match';

export default class Activities extends Component {
	fetch = () => {
		this.setState({ loading: true });
		fetch('https://api.cityguidr.com/users/login', {
			method: 'POST',
			mode: 'cors',
			cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
			credentials: 'same-origin', // include, *same-origin, omit
			headers: {
				'Content-Type': 'application/json; charset=utf-8'
			},
			redirect: 'follow', // manual, *follow, error
			referrer: 'no-referrer', // no-referrer, *client
			body: JSON.stringify({
				email: 'ivokroo@gmail.com',
				password: 'pass1234'
			})
		})
			.then(response => response.json())
			.then(json => {
				this.setState({ loading: false });
				console.log(json);
			});
	};

	constructor() {
		super();
		this.state = {
			loading: false
		};
	}
	componentDidMount() {
		this.fetch();
		// const user = localStorage.getItem('user');
		// console.log(JSON.parse(user));
	}

	render() {
		return (
			<div class={style.home}>
				{!this.state.loading ? (
					<div>
						<div class={style.headerHolder}>
							<h1>All Activities</h1>
							<div class={style.buttonHolder}>
								<Link href="/activities/create">
									<Button title="add" />
								</Link>
								<Button title="search" />
							</div>
						</div>
						<table>
							<tr>
								<th>NAME</th>
								<th>EMAIL</th>
								<th class={style.centerd}>CONTACTED</th>
								<th class={style.centerd}>ACCEPTED</th>
							</tr>
							<tr>
								<td>
									<b>Dutch pinball museum</b>
								</td>
								<td>info@dutchpinballmuseum.nl</td>
								<td class={style.centerd}>✓</td>
								<td class={style.centerd}>✓</td>
							</tr>
							<tr>
								<td>
									<b>Dutch pinball museum</b>
								</td>
								<td>info@dutchpinballmuseum.nl</td>
								<td class={style.centerd}>✓</td>
								<td class={style.centerd}>✓</td>
							</tr>
							<tr>
								<td>
									<b>Dutch pinball museum</b>
								</td>
								<td>info@dutchpinballmuseum.nl</td>
								<td class={style.centerd}>✓</td>
								<td class={style.centerd}>✓</td>
							</tr>
						</table>
					</div>
				) : (
					<h1>LOADING...</h1>
				)}
			</div>
		);
	}
}



